
























































































































































































import { Vue, Component } from 'vue-property-decorator';

import { EFormType, EFormMode, ETableSorting, ETableRowType, EPageStep, EPageActionLicense, EDatetimeFormat } from '@/../enums';
import {
    ServiceResponseFilter,
    ServiceServerConfig,
    ServiceFormConfig,
    ServiceToast,
    ServiceResolveText,
    ServiceDialog,
    ServiceBase64,
    ServiceDatetime,
} from '@/../helpers';
import { IComponent } from '@/../models';

import { ServerApiPath } from '@/config';

import Loading from '@/../components/loading/loading.vue';
import Table from '@/../components/table/table.vue';
import Forms from '@/../components/form/forms.vue';
import ImageAdvancedCropper from '@/../components/image/image_advanced_cropper.vue';
import Card from '@/../components/card/card.vue';

import { ModelsLicense as Model } from './models';

import { saveAs } from 'file-saver';

@Component({
    components: { Table, Card, Forms, ImageAdvancedCropper },
})
export default class VuePageClass extends Vue {
    //#region Variables & Computed
    private tableItem: IComponent.ITableItem = {
        path: ServerApiPath.SettingLicense_RU,
        showPaging: true,
        showCheckbox: false,
        checkboxDisabled: true,
        datas: [],
        columns: [],
        paging: {
            pageSize: 10,
            page: 1,
            totalPages: 0,
            total: 0,
        },
        actions: {
            disableEdit: true,
            disablePerview: true,
            disableDelete: false,
        },
    };

    private tableParam: IComponent.ITableParam = {
        page: 1,
        pageSize: 10,
        sortingField: undefined,
        sortingOrderEnum: ETableSorting.none,
        sortingOrder: undefined,
    };

    private tableApiParam: Model.ITableApiParam = {
        paging: {
            page: 1,
            pageSize: 10,
        },
        sorting: {},
    };

    private tableSelectedData: Model.ITableData[] = [];

    private pageItem: IComponent.IPageItemLicense = {
        page: EPageStep.table,
        action: EPageActionLicense.online,
    };
    private get pageTable(): boolean {
        return this.pageItem.page === EPageStep.table;
    }
    private get pageForm(): boolean {
        return this.pageItem.page === EPageStep.form;
    }

    // select option
    private selectOptionItem: Model.ISelectOptionItem = {
        mac: [],
    };

    private formItem: Model.IFormItem = {
        isClearState: false,
        fileState: null,
        date: 'YYYY/MM/DD',
    };

    private formDataOriginal: Model.IFormData = {
        key: '',
        mac: null,
        data: '',
        file: null,
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private formConfig: Model.IFormConfigs = {
        key: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        mac: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        data: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfigs: IComponent.IFormConfig[] = [];

    private formHidden: Model.IFormHidden = { key: null, mac: null, data: null };

    private formButtonDisabled: Model.IFormButtonDisabled = { key: true, mac: true };
    private get formSaveDisable(): boolean {
        const disabled: Model.IFormButtonDisabled = this.formButtonDisabled;
        switch (this.pageItem.action) {
            case EPageActionLicense.online:
                if (disabled.key || disabled.mac) return true;
                break;
            case EPageActionLicense.offline:
                if (!this.formData.data) return true;
                break;
            default:
                break;
        }

        return false;
    }

    private get formShowDownloadButton(): boolean {
        return this.pageItem.action === EPageActionLicense.download;
    }

    private get formFileAcceptType(): string {
        let result: string = '.lic,.license';
        return result;
    }

    private eFormMode = EFormMode;
    private loading = new Loading();
    //#endregion

    //#region Vue Life
    private async beforeMount(): Promise<void> {
        await this.initTable();
        await this.initSelectOption();
        await this.initData();
    }
    //#endregion

    //#region Funtion

    //#region Page
    private pageToList(): void {
        this.pageItem.page = EPageStep.table;
        this.pageItem.action = EPageActionLicense.list;

        this.formDataClear();
    }

    private async pageToOnline(): Promise<void> {
        this.pageItem.page = EPageStep.form;
        this.pageItem.action = EPageActionLicense.online;

        this.formItem.isClearState = true;
        await this.formConfigSet();
    }

    private async pageToOffline(): Promise<void> {
        this.pageItem.page = EPageStep.form;
        this.pageItem.action = EPageActionLicense.offline;

        this.formItem.isClearState = true;
        await this.formConfigSet();
    }

    private async pageToDdownload(): Promise<void> {
        // step
        this.pageItem.page = EPageStep.form;
        this.pageItem.action = EPageActionLicense.download;
        this.formItem.isClearState = true;
        await this.formConfigSet();
    }
    //#endregion

    //#region Init Table
    private async initTable(): Promise<void> {
        this.initTableColumns();
        this.initTableParam();
        await this.tableReload(this.tableParam);
    }

    private initTableColumns(): void {
        this.tableItem.columns = [
            { title: this.$i18n.Common_NO, type: ETableRowType.autoIndex },
            { title: this.$i18n.Settings_License_TitleKey, key: 'licenseKey' },
            { title: this.$i18n.Settings_License_Mac, key: 'mac' },
            { title: this.$i18n.Settings_License_Trial, key: 'trial' },
            { title: this.$i18n.Settings_License_RegisterDate, key: 'registerDate' },
            { title: this.$i18n.Settings_License_ExpirationDate, key: 'expireDate' },
        ];
    }

    private initTableParam(): void {
        const { paging, sorting }: IComponent.ITableItem = this.tableItem;

        this.tableParam = {
            page: paging?.page ?? 1,
            pageSize: paging?.pageSize ?? 10,
            sortingField: sorting?.field ?? undefined,
            sortingOrderEnum: sorting?.orderEnum ?? ETableSorting.none,
            sortingOrder: sorting?.order ?? undefined,
        };
    }
    //#endregion

    //#region Init Data
    private initData(): void {
        this.initFormConfig();
    }

    private initFormConfig(): void {
        this.formConfigs = [];

        // key
        this.formConfig.key.id = 'key';
        this.formConfig.key.label = this.$i18n.Settings_License_TitleKey;
        this.formConfig.key.placeholder = this.$i18n.Settings_License_TitleKey;
        this.formConfig.key.formType = EFormType.text;
        this.formConfig.key.required = true;

        // mac
        this.formConfig.mac.id = 'mac';
        this.formConfig.mac.label = this.$i18n.Settings_License_Mac;
        this.formConfig.mac.placeholder = this.$i18n.Settings_License_Mac;
        this.formConfig.mac.formType = EFormType.single;
        this.formConfig.mac.required = true;

        // data
        this.formConfig.data.id = 'data';
        this.formConfig.data.formType = EFormType.slot;

        this.formConfigs = [this.formConfig.key, this.formConfig.mac, this.formConfig.data];
    }
    //#endregion

    //#region Init Select Option
    private async initSelectOption(): Promise<void> {
        await this.initSelectOptionMacAddress();
    }

    private async initSelectOptionMacAddress(): Promise<void> {
        this.selectOptionItem.mac = [];

        let data = JSON.parse(JSON.stringify(NETWORKS));

        (data || []).forEach((item) => this.selectOptionItem.mac.push({ objectId: item.mac, name: `${item.mac} / IP: ${item.address}` }));
    }
    //#endregion

    //#region Table
    private async tableGetApiData(tableParam: IComponent.ITableParam): Promise<void> {
        const { page, pageSize, sortingField, sortingOrder }: IComponent.ITableParam = tableParam;

        this.tableItem.paging.page = page ?? this.tableItem.paging.page;
        this.tableItem.paging.pageSize = pageSize ?? this.tableItem.paging.pageSize;

        const { paging }: IComponent.ITableItem = this.tableItem;

        this.tableApiParam.paging.page = paging.page ?? 1;
        this.tableApiParam.paging.pageSize = paging.pageSize ?? 10;

        this.tableApiParam.sorting = {};
        if (sortingField !== undefined && sortingOrder !== undefined) {
            this.tableApiParam.sorting.field = sortingField;
            this.tableApiParam.sorting.order = sortingOrder;
        } else {
            delete this.tableApiParam.sorting;
        }

        let data = this.getFakeData(tableParam);
        this.tableSetData(data);

        this.formItem.isClearState = true;

        this.loading.hide();
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(LICENSES));

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return data;
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        const { page, pageSize, sortingField, sortingOrder }: IComponent.ITableParam = tableParam;

        this.tableParam.page = page ?? this.tableItem.paging.page;
        this.tableParam.pageSize = pageSize ?? this.tableItem.paging.pageSize;
        this.tableParam.sortingField = sortingField ?? undefined;
        this.tableParam.sortingOrder = sortingOrder ?? undefined;

        await this.tableGetApiData(this.tableParam);
    }

    private tableSetData(result: any[]): void {
        let tableData: Model.ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId || '',
                licenseKey: element.licenseKey ? element.licenseKey : '',
                mac: element.mac,
                count: element.count ? element.count : null,
                trial: ServiceResolveText.resolveBooleanToString(element.trial, this),
                registerDate: element.registerDate
                    ? ServiceResolveText.resolveDate(element.registerDate, EDatetimeFormat.datetime_slash_YYYYMMDDHHmmss)
                    : '',
                expireDate: element.expireDate
                    ? ServiceResolveText.resolveDate(element.expireDate, EDatetimeFormat.datetime_slash_YYYYMMDDHHmmss)
                    : '',
                expired: ServiceResolveText.resolveBooleanToString(element.expired, this),
            });
        });

        this.tableItem.datas = tableData;
    }

    private tableDeleteButtonDisabled(): boolean {
        return this.tableSelectedData.length > 0 ? false : true;
    }

    private tableClickCheckbox(selectedData: Model.ITableData[]): void {
        this.tableSelectedData = selectedData;
    }
    //#endregion

    //#region Form
    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
        this.formItem.isClearState = false;
        this.formItem.fileState = null;
    }

    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        this.formData[id] = value;
        this.formButtonDisabled[id] = disabledButton;
    }

    private async formOfflineFileChange(data: any) {
        let result = await ServiceBase64.getBase64(data);
        this.formData.data = result.result as string;
    }

    private async formConfigSet(data: any = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
        switch (this.pageItem.action) {
            case EPageActionLicense.online:
                this.formHidden = {
                    data: true,
                    key: false,
                    mac: false,
                };
                break;

            case EPageActionLicense.offline:
                this.formHidden = {
                    data: false,
                    key: true,
                    mac: true,
                };
                break;

            case EPageActionLicense.download:
                this.formHidden = {
                    data: true,
                    key: true,
                    mac: false,
                };
                break;

            default:
                break;
        }
    }

    private async formDataSet(data: Model.ITableData): Promise<void> {}

    private formTitle(): string {
        switch (this.pageItem.action) {
            case EPageActionLicense.online:
                return this.$i18n.Settings_License_AddOnline;

            case EPageActionLicense.offline:
                return this.$i18n.Settings_License_AddOffline;

            case EPageActionLicense.download:
                return this.$i18n.Settings_License_DownLoad_HardwareId;

            default:
                return '';
        }
    }

    //#endregion

    private async dataSave(): Promise<void> {
        let data: Model.IFormData = this.formData;

        interface IDataSaveParam {
            key?: string;
            mac?: string;
            data?: string;
        }

        let dataParam: IDataSaveParam = {};

        switch (this.pageItem.action) {
            case EPageActionLicense.online:
                dataParam.key = data.key;
                dataParam.mac = data.mac.objectId;
                break;
            case EPageActionLicense.offline:
                dataParam.data = data.data;

                //#region for demo
                dataParam.key = 'LBHSN-ICLDC-NSFFL-HBFZS';
                dataParam.mac = 'D0:17:C2:95:EC:32';
                //#endregion
                break;
            default:
                break;
        }

        this.loading.show();

        LICENSES.push({
            objectId: new Date().toISOString(),
            licenseKey: dataParam.key,
            mac: dataParam.mac,
            brand: '0000',
            productNO: '00258',
            productName: 'FRS Edge',
            count: 1,
            trial: false,
            registerDate: ServiceDatetime.toString(new Date(), 'YYYY/MM/DD'),
            expireDate: '',
            expired: false,
        });

        this.loading.hide();
        ServiceToast.success(this.$i18n.Settings_License_AddSuccess);
        await this.tableReload(this.tableParam);
        this.pageToList();
    }

    private async dataDelete(rowData: Model.ITableData): Promise<void> {
        let content: string = this.$i18n.Dialog_ConfirmDelete;
        let title: string = this.$i18n.Dialog_Confirm;

        let param: { data: { objectId: string } } = { data: { objectId: new Date(rowData.registerDate).toISOString() } };

        ServiceDialog.Question(this, content, title)
            .then(async (result) => {
                if (result.value) {
                    // const url: string = ServiceServerConfig.paramUrl(this.tableItem.path);
                    // try {
                    //     const { status, data }: any = await this.axios.delete(url, param);
                    //     if (status === 200) {
                    //         this.loading.hide();
                    //         let _data: IComponent.IStatusCodeObjectIdMessageResponse[] = data?.datas;
                    //         await this.tableReload(this.tableParam);
                    //         this.pageToList();
                    //     }
                    // } catch (e) {
                    //     ServiceResponseFilter.catchError(this, e);
                    // } finally {
                    //     this.loading.hide();
                    // }

                    this.loading.show();

                    // modify fake data
                    if (Array.isArray(rowData)) {
                        rowData.forEach((item) => {
                            let targetIndex: number = LICENSES.findIndex((item) => item.objectId === item.objectId);
                            LICENSES.splice(targetIndex, 1);
                        });
                    } else {
                        let targetIndex: number = LICENSES.findIndex((item) => item.objectId === rowData.objectId);
                        LICENSES.splice(targetIndex, 1);
                    }

                    this.loading.hide();

                    ServiceToast.success(this.$i18n.Dialog_DeleteSuccess);
                    this.pageToList();
                    await this.tableReload(this.tableParam);
                }
            })
            .catch((e: any) => console.error(e));
    }

    private dataDownloadHardwareId(): void {
        if (!this.formData.mac.objectId) '';
        // const Url: string = `${ServerApiPath.Settings_License_Product_Id}?mac=${this.formData.mac.objectId}`;

        const PublicPath: string = __webpack_public_path__ || '/./';
        const Url: string = `${window.location.origin}${PublicPath}setting.json`;

        let result = this.formData.mac.objectId;
        const regex: RegExp = /:/gi;
        result = result.replace(regex, '-');
        result = `ProductID_${result}.txt`;

        const xhr = new XMLHttpRequest();
        xhr.open('GET', Url, true);
        xhr.responseType = 'blob';
        xhr.setRequestHeader('Authorization', 'Basic a2VybWl0Omtlcm1pdA==');
        xhr.onload = () => {
            if (xhr.status === 200) {
                saveAs(xhr.response, result);
            }
        };

        xhr.send();
    }

    //#endregion
}

